<template>
    <div class="app">
        <navbar title="微信二维码" v-if="$phonemodel == 'Android'"></navbar>
        <div class="contain" v-if="setNew" :class="{nav:$phonemodel == 'Android'}">
            <div class="title">上传微信二维码</div>
            <div class="subContain pr">
                <img src="@/static/images/my/shangchuan_image.png" alt  />
                <input @change="headImgChange" type="file" accept="image/*" id="upBtn" />
            </div>
        </div>
        <div class="contain" v-else :class="{nav:$phonemodel == 'Android'}">
            <div class="title" @click="setNew = true">重新上传</div>
            <div class="subContain pr">
                <img :src="wechat" alt  />
            </div>
        </div>
    </div>
</template>

<script>
    import navbar from "@/components/navbar";
    import { toast, Dialog } from 'vant';
    import {upwechat} from "../../api/center";
    import { mapGetters } from 'vuex'
    export default {
        name: "Wechat",
        components:{
            navbar
        },
        data(){
            return {
                walletData:{},
                setNew:true
            }
        },
        mounted()
        {
            this.setNew = this.wechat ? false : true
        },
        computed: {
            ...mapGetters([
                'wechat',
            ])
        },
        methods:{
            headImgChange( e ) {

                let file = e.target.files[0]
                /* eslint-disable no-undef */
                let param = new FormData() // 创建form对象
                param.append('file', file, file.name) // 通过a
                upwechat(param).then(res => {

                    this.setNew = false
                    this.$store.dispatch('user/setWechat', res.imgUrl).then(() => {

                        toast('上传成功')
                    }).catch(() => {
                        toast('上传失败')
                    })
                })
            },
        }
    }
</script>

<style scoped>
      @import "../../static/css/wechat.css";
</style>



